.todoinput{
  display:flex;
  justify-content: center; 
  width:100%;

}
.group-input{
  width:100%;
  display:flex;
  
}
input[type='text']{
  width:330px;  
  line-height: 2;
  border: .1rem solid grey;
  
  text-align: center;
}
.input-logo{
   width:40px;
   height:40px; 
}
.btn-container{
   width:100%; 
   margin:1rem 0rem;
   
}
 .color{
    background-color: rgb(247, 82, 82);
 }
 .green{
    background-color: rgb(86, 212, 86);
 }
 
.btn{
  width:100%;  
  
  border-style: none;
  
  border-radius: .4rem;
  padding:.5rem 1rem;
  cursor: pointer;
}
.list-item{
   width:100%; 
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.button-container{
   width:40%; 
   display:flex;
    justify-content: space-between;  
}
.edit{
  background-color: lightgreen;
  border-style: none;
  
  padding:.5rem .8rem;
  border-radius: .5rem;
  cursor: pointer;
}
.delete{
    background-color:rgb(230, 69, 69);
    border-style: none;
    
    padding:.5rem .8rem;
    border-radius: .5rem;
    cursor: pointer;
  }
.todoTask{
 width:100%;
 margin:0 auto;
}
.render-list{
  width:336px;
  margin:0 auto;  
}
.container{
  margin:0 auto;  
  width:336px;
}

.msg{
  background-color: rgb(219, 134, 134); 
  border-radius: .5rem;
  line-height: 2.5;
  width:30%;
  margin:1rem auto;

}
